import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ACTION } from '../enums/action.enum';
import { ERaseedDashboardComponent } from './e-raseed-dashboard.component';

const routes: Routes = [
  { path: '',
    component: ERaseedDashboardComponent,
    data: {
      title: "Allow Purpose",
      permission: [
        ACTION.ADD,
        ACTION.EDIT,
      ],
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ERaseedDashboardRoutingModule { }
