import { Injector } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { environment } from 'src/environments/environment';
import { BaseJoinAction } from '../class/base-join-actions';
@Component({
  selector: 'di-component-name-here',
  templateUrl: './e-raseed-dashboard.component.html',
  styleUrls: ['./e-raseed-dashboard.component.css']
})
export class ERaseedDashboardComponent extends BaseJoinAction implements OnInit {

  public module_list_data = [];
  public logout = environment.UserProfile + 'Login/logout';

  constructor(
    injector : Injector,
    _router: Router,
    private cookie:CookieService
  ){
    super(injector);
  }

  ngOnInit(): void {
    this._DashBoard();
    this.getErseedModuleList();
  }

  getErseedModuleList(){
    const current_module = this.cookie.get('current_module');
    this._http.get({
      endpoint: this.URLz.MODULE_LIST,
      query: {
        current_module,
      }
    }).subscribe((res : any)=>{
        this.module_list_data = res.data.records;
    })
  }

  clearLocalStorage() {
    localStorage.clear();
  }

  _DashBoard(){
    if(this.cookie.get('current_module') == '2')  this._router.navigateByUrl('/dm_dashboard');
    else if(this.cookie.get('current_module')  == '')  window.location.href = this._fhs._logout();
    else  this._router.navigateByUrl('/e_raseed_dashboard');
  }

}
